	import Swiper from '../assets/swiper/swiper-3.4.2.min';
	import _ from 'lodash';
	
	var myswiper = null;
	
	export default{
		props:{
			list:Array,
			default:function(){
				return []
			},
			config:{
				type:Object,
				default:function(){
					return{
						autoplay:3000,
						observer:true,
						observeParents:true,
						autoplayDisableOnInteraction:false,
						loop:true
					}
				}
			}
		},
		data(){
			return{
				
			}
		},
		mounted(){
//			console.log(this.list)
			myswiper = new Swiper (this.$refs.swiper, _.assign({
		        pagination: this.$refs.pagination
		     }, this.config))
		},
		watch:{
			list(newVal,oldVal){
		        setTimeout(function () {
		          myswiper.update();
		        }, 10)				
			}
		}
		
		
		
		
	}
	



