import Vue from 'vue'
import Vuex from 'vuex'
import shopCarTool from '../util/shop-car-tool'
var shopCar = new shopCarTool();

Vue.use(Vuex)

const ShopCar = { // 路由状态
  state: {
    length: shopCar.length(),
	count:0,
  },
  mutations: {
    setShopCarLength (state, length) {
      state.length = length
    },
	changeCount(state,num){
//		console.log(state)
		state.count +=  1;
	},
  },
  getters: {
    getShopCarLength: state => {
      return state.length
    },
    countAddTen:state => {
    	return state.count + 10;
    },
    countAddTen22:(state) => (num) => {
//  	console.log(state)
    	return num;
    }
  },
  actions:{
  	changeCount:({commit},num) => {
  		console.log(num);
  		setTimeout(()=>{
		    commit("changeCount",num)
		},1000)
  	}
  }
}

var moduleB = {
	state:{
		countB:111,
	},
	mutations:{
		changeCountB(state){
			state.countB++;
		}
	}
	
}

export default new Vuex.Store({
	modules:{
		ShopCar,
		moduleB
	}
	
	
})



