import Rem from '@/assets/js/rem';
import VueDB from './util/vue-db/vue-db-long';
new Rem();
var DB = new VueDB();

export default {
  name: 'App',
  data(){
  	return {
  		transitionName:"slide-go",
  		nowUrl:"/",
  		djjx:this.$t('home.topFire_src.title_djjx')
  	}
  },
  watch:{
			'$route'(to,from){
//				console.log(from)
//				console.log(to)
				if(from.query.time){
					if(to.query.time > from.query.time){
						this.transitionName = "slide-go";
					}else{
						this.transitionName = "slide-back";
					}
				}else{
					this.transitionName = "slide-go"
				}
				this.nowUrl = to.fullPath
			}
		},
	computed:{
		leaveComp(){
			var resout = false;
			var base = ['home','classStore','classification','mine',"login","register","aboutUs","hcDetail","forgetPassword","indent","mineOrder","mineOrderDetail","storeList","myAllOrder","userAddrList","addUserAddr"];
			
        for(var i=0; i<base.length; i++){
          if(this.isActive(base[i])){
            resout = false;
          }
        }
			if (this.isActive("classification") || this.isActive("closeAccount") ||  this.isActive("login") ||  this.isActive("register") || this.isActive("aboutUs") || this.isActive("hcDetail") || this.isActive("forgetPassword") || this.isActive("indent") || this.isActive("mineOrder") || this.isActive("mineOrderDetail") || this.isActive("storeList") || this.isActive("myAllOrder")|| this.isActive("userAddrList")|| this.isActive("addUserAddr")) {
				resout = true;
			}			
			
			return resout;
		}
	},
	methods:{
		isActive(name){
			return this.nowUrl.indexOf(name) != -1;
		},
		open(link){
			DB.setItem("isOrder","noOrder");
			DB.setItem("selectThisGoods",JSON.stringify({}));
			this.$router.openPage(link);
			
		}
	}
}



